<template>
  <div class="workMent">

    <el-table :data="tableData" v-loading="loading" border style="width: 100%">
      <el-table-column prop="id" label="用户ID"> </el-table-column>
      <el-table-column prop="userId" label="所属班级" width="180"> </el-table-column>
      <el-table-column prop="title" label="作业名称" :formatter="colFormatter"> </el-table-column>
      <el-table-column prop="completed" label="完成情况" :formatter="colFormatter"> </el-table-column>
    </el-table>
    <Pageing :total="total" :url="url" :loading="loading"/>

    
  </div>
</template>

<script>
import {getTableData} from '@/utils/table'
import Pageing from '@/components/common/Pageing.vue'

export default {

  components: {
    Pageing
  },

  data() {
    return {
      total: 0, //数据总条数
      // currentPage: 1,   //当前页码
      // pageSize: 10, //每页条数  
      url: "/works",
      
      loading: true,
      
      tableData: [],
     
      
    }
  },

  // created() {
  //   this.getData();
  // },

  methods: {
    getData() {
      this.loading = true;
      
      getTableData(this, '/works', {page: this.currentPage, size: this.pageSize}) 

    },

    //统一字典处理 (可以所有列都加上，通过column.property来判断具体字段)，通过后台获取所有字典，判断该字段是否在字典中
    colFormatter(row, column, cellValue, index) {
      // console.log(column.property)

      if(column.property == "completed"){
        let wcqk = ""
        if(cellValue){
          wcqk = "已完成"
        }else {
          wcqk = "未完成"
        }
        return wcqk
      }else {
        return cellValue;
      }
      

    },

    
  }

  

}
</script>

<style lang="scss" scoped>
  .workMent {
    .el-form-item {
      text-align: left;
    }
    .el-pagination {
      text-align: left;
      margin-top: 20px;
    }

  }
</style>