<template>
  <div>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getTableData } from "@/utils/table";

export default {
  props: {
    total: Number,
    url: String,
    loading: Boolean
  },

  data() {
    return {
      currentPage: 1, //当前页码
      pageSize: 10, //每页条数
    };
  },
  created() {
    this.getData();
  },

  methods: {
    getData() {
      this.$parent.loading=true
      getTableData(this.$parent, this.url, { page: this.currentPage, size: this.pageSize });
    },
    //分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.currentPage = 1;
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getData();
    },
  },
};
</script>

<style lang="scss" scoped>
.el-pagination {
  text-align: left;
  margin-top: 20px;
}
</style>